import {Ingredient} from '../shared/ingredient.model';
import {Subject} from 'rxjs/Subject';

export class ShopingListService {
  ingredientsChanched= new Subject<Ingredient[]>();
  startedEditing= new Subject<number>();
  private   ingradients: Ingredient[] = [
    new Ingredient('Apples', 5),
    new Ingredient('Tomates', 10)
  ];

  getIngredients(){
    return this.ingradients.slice();
  }
  getIngredient(index: number){
    return this.ingradients[index];
  }
  addIngredient(ingredient: Ingredient){
    this.ingradients.push(ingredient);
    this.ingredientsChanched.next(this.ingradients.slice());
  }
  addIngredients(ingredients: Ingredient[]) {
     this.ingradients.push(...ingredients);
     this.ingredientsChanched.next(this.ingradients.slice());
  }
  updateIngredient(index: number, newIngredient: Ingredient){
    this.ingradients[index] = newIngredient;
    this.ingredientsChanched.next(this.ingradients.slice());
  }
  deleteIngredient(index: number){
    this.ingradients.splice(index, 1);
    this.ingredientsChanched.next(this.ingradients.slice());
  }
}



