import {Injectable} from '@angular/core';
import {Http, Response} from '@angular/http';

import {RecipeService} from '../recipes/recipe.service';
import {Recipe} from '../recipes/recipe.model';
import 'rxjs/add/operator/map';
import {AuthSerice} from '../auth/auth.service';
@Injectable()
export class DataStorageService {
  constructor(private http: Http,
              private recipeService: RecipeService,
              private authService: AuthSerice){}

  storeRecipes(){
    const token = this.authService.getToken();
   return this.http.put('https://ng-recipe-book-90e68.firebaseio.com/recipes.json?auth='+token
      , this.recipeService.getRecipes());
  }

  getRecipes() {
   const token = this.authService.getToken();

    this.http.get('https://ng-recipe-book-90e68.firebaseio.com/recipes.json?auth=' +token)
      .map(
        (response: Response) => {
          const recipes: Recipe[] = response.json();
          for (const recipe of recipes){
            if (!recipe['ingredients']) {
              recipe['ingredients'] = [];
            }
          }
          return recipes;
        }
      )
      .subscribe(
        (recipes: Recipe[]) => {
          this.recipeService.setRecipes(recipes);
        }
      );
  }
}



