import {Recipe} from './recipe.model';
import {Injectable} from '@angular/core';
import {Ingredient} from '../shared/ingredient.model';
import {ShopingListService} from '../shopping-list/shoping-list.service';
import {Subject} from 'rxjs/Subject';

@Injectable()
export class RecipeService {
 recipedChanged= new Subject<Recipe[]>();

private  recipes: Recipe[]= [
    new Recipe('the test recipe',
      'a sumple recipe', 'http://ichef.bbci.co.uk/food/ic/food_16x9_88/recipes/chicken_and_vegetable_65850_16x9.jpg',
    [
      new Ingredient('Meat', 1),
      new Ingredient('French meat', 10)
    ]),
    new Recipe('my recipe',
      'a my sumple recipe',
      'https://www.bbcgoodfood.com/sites/default/files/styles/teaser_item/public/recipe_images/recipe-image-legacy-id--932456_11.jpg?itok=nszDU2JG',
    [
      new Ingredient('Buns', 2),
      new Ingredient('Meat', 5)
    ])
  ];
  constructor(private  slService: ShopingListService) {}
/*constructor(private slService: ShopingListService) {}*/

    setRecipes(recipes: Recipe[]){
      this.recipes = recipes;
      this.recipedChanged.next(this.recipes.slice());
    }

   getRecipes(){
     return this.recipes.slice();
   }
   getRecipe(index: number){
     return this.recipes[index];
   }
  addIngredientsToShopingList(ingredients: Ingredient[]){
     this.slService.addIngredients(ingredients);
  }
  addRecipe(recipe: Recipe){
    this.recipes.push(recipe);
    this.recipedChanged.next(this.recipes.slice());
  }
  updateRecipe(index: number, newRecipe: Recipe){
    this.recipes[index] = newRecipe;
    this.recipedChanged.next(this.recipes.slice());
  }
  deleteRecipe(index: number){
    this.recipes.splice(index, 1);
    this.recipedChanged.next(this.recipes.slice());
  }
}



